import React from "react";
import imgi from "../images/gallery/DSC01946.JPG";
const images = require.context("../images/gallery", false, /\.JPG$/);
const imagesArray = images.keys();

console.log(imagesArray);
console.log(imgi);

export default function Slider() {
  return (
    <div>
      {imagesArray.map((image, id) => (
        <img
          key={id}
          alt={"-"}
          style={{ width: "10px", height: "10px" }}
          src={image}
        />
      ))}

      <img src={imgi} alt={"no"} style={{ width: "100px", height: "100px" }} />
    </div>
  );
}
